.dice-roll {
  text-align: center;
}

.dice-roll-btn {
  appearance: none;
  padding: 8px 16px;
  font-size: 24px;
  line-height: 1;
  vertical-align: middle;
  background: darkorange;
  color: #fff;
  border: none;
  cursor: pointer;
}

.dice-roll-btn:hover {
  box-shadow: 0 1px 6px #999;
}

.dice-roll-result {
  height: 64px;
  font-size: 64px;
  line-height: 64px;
  vertical-align: middle;
  color: darkcyan;
  transition: all .3s ease-in;
}

.dice-roll-rolling .dice-roll-btn {
  box-shadow: none;
}

.dice-roll-rolling .dice-roll-result {
  font-size: 0;
}



